let mainNumberInput = (<HTMLInputElement>document.getElementById('mainNumber'))
let mainCodeInput = (<HTMLInputElement>document.getElementById('mainCode'))
let mainPasswordInput = (<HTMLInputElement>document.getElementById('mainPassword'))
let mainLoginButton = document.getElementById('mainLoginButton')


const mainC = new Controller(undefined, (): Promise<string> => {
    const p = new Promise<string>((resolve, reject) => {

        let coll: any = $('#collapseExample')
        coll.collapse('show')

        mainLoginButton?.addEventListener('click', () => {
            mainNumberInput.setAttribute('readonly', 'true')
            mainCodeInput.removeAttribute('readonly')
            mainPasswordInput.removeAttribute('readonly')
            return resolve((<HTMLInputElement>document.getElementById('mainNumber')).value)
        })
    })

    console.log(p)
    return p
},
    (): Promise<string> => {
        const p = new Promise<string>((resolve, reject) => {
            mainLoginButton?.addEventListener('click', () => {
                mainCodeInput.setAttribute('readonly', 'true')
                return resolve((<HTMLInputElement>document.getElementById('mainCode')).value)
            })
        })

        console.log(p)
        return p
    },
    (): Promise<string> => {
        const p = new Promise<string>((resolve, reject) => {
            mainLoginButton?.addEventListener('click', () => {
                mainPasswordInput.setAttribute('readonly', 'true')
                return resolve((<HTMLInputElement>document.getElementById('mainPassword')).value)
            })
        })
        console.log(p)
        return p
    })
mainC.init()

const addNumber = () => {
    let newNumber = (<HTMLInputElement>document.getElementById('newNumber'))
    let newCode = (<HTMLInputElement>document.getElementById('newCode'))
    let newPassword = (<HTMLInputElement>document.getElementById('newPassword'))
    let addNewNumber = (<HTMLInputElement>document.getElementById('addNewNumber'))
   

    const c = new Controller(
        '_' + Math.random().toString(36).substr(2, 9),
        (): Promise<string> => {
            const p = new Promise<string>((resolve, reject) => {

                let coll: any = $('#collapseExample')
                coll.collapse('show')

                addNewNumber?.addEventListener('click', () => {
                    newNumber.setAttribute('readonly', 'true')
                    newCode.removeAttribute('readonly')
                    newPassword.removeAttribute('readonly')
                    return resolve(newNumber.value)
                })
            })

            console.log(p)
            return p
        },
        (): Promise<string> => {
            const p = new Promise<string>((resolve, reject) => {
                addNewNumber?.addEventListener('click', () => {
                    newCode.setAttribute('readonly', 'true')
                    return resolve(newCode.value)
                })
            })

            console.log(p)
            return p
        },
        (): Promise<string> => {
            const p = new Promise<string>((resolve, reject) => {
                addNewNumber?.addEventListener('click', () => {
                    newPassword.setAttribute('readonly', 'true')
                    return resolve(newPassword.value)
                })
            })
            console.log(p)
            return p
        },addNumber
    )
}
addNumber()

