    async saveInstances(): Promise<void> {

        console.log('saving instance....', this.airgram.provider.config.instanceName)

        await Controller.airgram.api.getChats({ limit: 100, offsetChatId: 0, offsetOrder: '9223372036854775807' })
        let instanceList = (<HTMLInputElement>document.getElementById('instanceList'))
        const formatter = new JSONFormatter(this.me, 0);
        instanceList.append(formatter.render())

        Controller.instancesList = await Controller.getInstances()
        if (!Controller.instancesList.includes(this.airgram.provider.config.instanceName)) {
            Controller.instancesList.push(this.airgram.provider.config.instanceName)
            console.log('instanceName::', this.airgram.provider.config.instanceName)
        }

        Controller.airgram.api.searchChatMessages({ chatId: Controller.ltm, query: '["mainInstance', limit: 100 }).then(toObject)
            .then(async (msgs: any) => {
                console.log('msgs', msgs)
                for (let key in msgs.messages) {
                    console.log('key:', key)
                    if (msgs.messages[key] != msgs.messages[0].id) {
                        Controller.airgram.api.deleteMessages({ chatId: Controller.ltm, messageIds: [msgs.messages[key].id], revoke: true })
                            .catch((r: any) => {
                                console.log(r);
                            })
                    }
                }
                if (msgs.totalCount > 1) {
                    Controller.airgram.api.editMessageText({
                        chatId: Controller.ltm,
                        messageId: msgs.messages[0].id,
                        inputMessageContent: {
                            _: 'inputMessageText',
                            text: {
                                _: 'formattedText',
                                text: JSON.stringify(Controller.instancesList)
                            }
                        }
                    }).then((res: any) => {

                        console.log(res)
                    }).catch((r: any) => {
                        console.log(r);

                    })
                } else {
                    Controller.airgram.api.sendMessage({
                        chatId: Controller.ltm,
                        inputMessageContent: {
                            _: 'inputMessageText',
                            text: {
                                _: 'formattedText',
                                text: JSON.stringify(Controller.instancesList)
                            }
                        }
                    }).then((res: any) => {
                        console.log(res)
                    }).catch((r: any) => {
                        console.log(r);

                    })
                }
            }).catch((error: any) => {
                console.log(error)
            })
    }

    static getInstances = async (): Promise<any> => {
        await Controller.airgram.api.getChats({ limit: 100, offsetChatId: 0, offsetOrder: '9223372036854775807' })
        try {
            let res: any = toObject(await Controller.airgram.api.searchChatMessages({ chatId: Controller.ltm, query: 'mainInstance', limit: 1 }))
            return Promise.resolve(Controller.instancesList = JSON.parse(res.messages[0].content.text.text))
        } catch (error) {
            console.log(error)
            return Promise.reject(error)
        }
    }

