import JSONFormatter from 'json-formatter-js'

export class Stats {
    public dbName:string
    private db: any;
    public logStore: any;
    private tx: any;
    private request: any
    private debugEl = $('#statsDebug')[0]

    constructor() {
        this.dbName = 'stats'
        this.request = indexedDB.open(this.dbName, 1);
        this.request.onerror = (error: any) => {
            console.log(error)
        }
        this.request.onupgradeneeded = (event: any) => {
            this.db = event.target.result;
            this.logStore = this.db.createObjectStore("log", { keyPath: "dateTime" });
        }
        this.request.onsuccess = (event: any) => {
            this.db = event.target.result;
            this.tx = this.db.transaction('log', 'readwrite');
            this.logStore = this.tx.objectStore('log');
        }
        // this.debug()
    }


    public saveToLogStore = (data: any) => {
        let request = indexedDB.open(this.dbName)
        let dateNow = new Date(Date.now())
        data.dateTime=dateNow.getTime()
        console.log(dateNow.toString())
        request.onsuccess = (event: any) => {
            let db = event.target.result;
            let tx = db.transaction('log', 'readwrite');
            let logStore: any = tx.objectStore('log');
            logStore.add(data)
            db.close()
        }
    }

    public getLogs = () => {
        let request = indexedDB.open(this.dbName)
        let logs: any = [];
        let p = new Promise((resolve,reject)=>{
            request.onsuccess = (event: any) => {
                let db = event.target.result;
                let tx = db.transaction('log', "readwrite")
                let logStore = tx.objectStore("log")
                logStore.openCursor().onsuccess = function (e: any) {
                    let cursor = e.target.result;
                    if (cursor) {
                        let dateNow = new Date(Date.now())
                        const logData = cursor.value
                        let logDataDateTime = new Date(logData.dateTime)
                        var diff = dateNow.valueOf() - logDataDateTime.valueOf();
                        var diffInHours = (diff/1000/60/60).toFixed(1);
                        logData.hoursAgo=diffInHours
                        logData.minutesAgo=(diff/1000/60).toFixed(1);
                        logData.timeStamp=logDataDateTime.toString()
                        logs.push(logData);
                        cursor.continue();
                    }
                    else {
                        resolve(logs)
                        console.log("Got all logs: "/*, logs*/);
                    }
                };
                db.close()
            }
        })
        return p
    }

    public  debug= async ()=>{
        let logs:any = await this.getLogs()
        this.debugEl.innerHTML=''
        const formatter = new JSONFormatter(logs);
        this.debugEl.append(formatter.render())
        let countBtn = $(`<button>count filter</button>`)[0]
        countBtn.addEventListener('click',()=>{
            let logs = this.countEventsInPrevHours(48)
            const formatter = new JSONFormatter(logs);
            this.debugEl.append(formatter.render())
        })
        this.debugEl.append(countBtn)
    }

    public countEventsInPrevHours = async (hours:any,phoneNumber?:number,event?:string):Promise<any>=>{
        let logsF:any=[]
        let p =new Promise((resolve,reject)=>{
            this.getLogs().then((logs:any)=>{
                logs.forEach((log:any) => {
                    if(phoneNumber){
                        if(log.hoursAgo < hours+1 && log.phoneNumber == phoneNumber){
                            console.log('pushing')
                            if(event){
                                if(log.event==event){
                                    logsF.push(log)
                                }
                            }else{
                                logsF.push(log)
                            }
                        }
                    }else{
                        if(log.hoursAgo < hours+1){
                            logsF.push(log)
                        }
                    }
                });
                resolve(logsF)
            })
        })
        p.then((logs:any)=>{
            console.log('logsF:',logs)
            return Promise.resolve(logs.length)
        })
        return p
    }
    
}